/* App.css */
body {
    font-family: Arial, sans-serif;
    margin: 0;
    padding: 0;
    background-color: #f4f4f4;
  }
  
  .app-header {
    background-color: #d4a5a5; /* Pastel purple */
    color: white;
    padding: 20px;
    text-align: center;
    font-size: 24px;
  }
  
  .app-footer {
    background-color: #8b7e7e; /* Darker complementary color */
    color: white;
    text-align: center;
    padding: 10px;
    position: fixed;
    width: 100%;
    bottom: 0;
  }
  
  .main-content {
    padding: 20px;
    min-height: 80vh;
    margin-top: 60px; /* Ensure there is space for the fixed footer */
  }
  
  .card-list {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center; /* Center the cards */
  }
  
  .card-list div {
    background: #bad6cf; /* Pastel green */
    padding: 20px;
    border-radius: 8px;
    box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
    width: calc(33.333% - 40px); /* Adjusted for gap and centering */
    margin-bottom: 20px;
    color: #333; /* Text color */
    border: 1px solid #ccc; /* Light border */
  }
  
  .no-cards {
    font-size: 18px;
    color: #555;
    text-align: center;
  }
  